
import { computed, defineComponent, reactive, ref } from 'vue';
export default defineComponent({
  emits: ['submitRefund', 'cancel'],
  props: {
    order: {
      type: Object,
      required: true
    }
  },
  setup(props, { emit }) {
    const delivery = computed(() => {
      let delivery = '';
      switch (props.order.deliveryType) {
        case 0:
          delivery = '快递';
          break;
        case 1:
          delivery = '物流';
          break;
        case 2:
          delivery = '自提';
          break;
        case 3:
          delivery = '送货上门';
          break;
      }
      return delivery;
    });
    const refundData = reactive({
      name: '',
      number: ''
    });
    const checked = ref('0');
    const onSubmit = (values: any) => {
      emit('submitRefund', values);
    };
    const cancel = () => {
      emit('cancel');
    };
    return {
      onSubmit,
      refundData,
      cancel,
      checked,
      delivery
    };
  }
});
